/* 整体容器 */
.post-list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0.95em;
}

/* 每篇文章项 */
.post-item {
  margin-bottom: 16px;
  position: relative;
}

.post-item a {
  text-decoration: none;
}



.post-link {
  border-bottom: 1px solid transparent;
  border-image: url('/images/border.svg') 1 round;
  transition: background-color 0.2s ease;
}

.item-posts ul {
    padding-inline-start: 0 !important;
    padding-left: 0 !important;
    margin: 0 0 0.8rem 0;
    list-style: none;
}

.item-posts li {
    margin-left: 0 !important;
    border-bottom: 2px dashed #b39eb5;
    padding-bottom: 0.2rem;
    margin-bottom: 0.2rem;

}

.post-meta {
  display: flex;
  align-items: center;
  font-size: 0.85em;
  color: #999;
  margin-bottom: 4px;
  flex-wrap: nowrap; 
  overflow: hidden; 
}

.post-title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 0; 
  max-width: calc(100% - 60px); 
}

.post-date {
  color: #999;
  font-size: 13px;
  font-weight: normal;
  flex-shrink: 0;
  margin-right: 4px;
}

.post-title-sep {
  color: #999;
  margin: 0 4px;
  flex-shrink: 0;
}

/* 标签容器 */
.post-tags {
  color: #666;
  font-size: 0.9em;
  white-space: nowrap;
}

.post-tag {
  margin: 0 2px;
}

/* 更多链接 */
.post-more {
  text-align: right;
  margin: 20px 0;
}

.post-more-link {
  color: #007acc;
  text-decoration: none;
  font-size: 0.9em;
}

.post-more-link:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .post-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }

  .post-date,
  .post-title-sep {
    margin: 0;
  }

  .post-title {
    white-space: normal;
    overflow: visible;
    text-overflow: clip;
    flex-shrink: 1;
  }
}