* {
    // cursor: url("https://64.media.tumblr.com/1f40badff67b539f91dcf106caecd24b/tumblr_inline_mo87wyItqZ1qz4rgp.gif"), crosshair;
    box-sizing: border-box;
}
.container {
    display: grid;
    grid-template-columns: 0.6fr 1.4fr 1fr;
    grid-template-rows: auto auto 1fr;
    gap: 0px;
    grid-auto-flow: row;
    justify-content: space-evenly;
    grid-template-areas:
        "Header Header Header"
        "Sidebar Content Content"
        "Sidebar Content Content"
        "Footer Footer Footer";
    width: 100%;
    height: 100%;
}

.container ul {
  padding-inline-start: 0 !important;
  padding-left: 0 !important;
  margin: 0 0 0.2rem 0;
//   list-style: none;
}


.container img {
  display: inline-block;
  vertical-align: middle; /* 关键：让图片和文字垂直居中对齐 */
}

textarea {
    width: 90%;
    margin-top: 5px;
}

.Header {
    grid-area: Header;
    width: 100%;
    height: 100%;
    background-color: transparent;
    text-align: center;
    font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-size: 80px;
    color: #1a4b3e;
    margin: 0;
    padding: 0;
}


.Content {
    justify-self: center;
    align-self: stretch;
    grid-area: Content;
    width: 100%;
    height: 100%;
    background-color: #f8fff8;
    border-top-right-radius: 20px;
    border-style: dashed dashed none none;
    border-color: #1a4b3e;
    font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-size: 15px;
    padding-left: 10px;
    padding-top: 15px;
}

.Content img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.Sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-area: Sidebar;
    width: 240px;
    height: 100%;
    background-color: #e8f5e8;
    border-color: #1a4b3e;
    border-top-left-radius: 20px;
    border-style: dashed none none dashed;
    font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-size: 15px;
    text-align: center;
    padding-top: 20px;
}

.Footer {
    justify-self: center;
    grid-area: Footer;
    width: 100%;
    height: 100%;
    background-color: #d0f0c0;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-style: dashed dashed dashed dashed;
    border-color: #1a4b3e;
    font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-size: 16px;
    text-align: center;
    padding-top: 10px;
}


b {
    color: white;
    background-color: #1a4b3e;
}

i {
    color: #8b5a2b;
}

u {
    text-decoration-color: #0004ff;
}

del {
    color: red;
}

.chat {
    position: relative;
    width: 80%;
    height: 100%px;
    border: 2px dotted black;
    margin-bottom: 20px;
    padding: 5px;
}

.border-text {
    font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    position: absolute;
    top: -15px;
    background-color: #e8f5e8;
    right: 10px;
    padding: 0 5px;
    font-size: 11px;

}

.border-text img {
    vertical-align: middle;
}

.change {
    height: 100px;
    overflow: auto;
    text-align: left;
    font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    padding: 5px;
}

.links_list {
    margin: 0px;
    padding: 0px;
    padding-bottom: 20px;
    width: 70%;
}

.links_head {
    padding-top: 2px;
    padding-bottom: 2px;
    position: relative;
    margin: 0px;
    text-align: center;
    font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-weight: bold;
    font-size: 1.1em;
    color: #fff;
    text-shadow: -1px 0 #424242, 0 1px #424242, 1px 0 #424242, 0 -1px #424242, 0 0;
    background: #d0f0c0;
    border: 1px solid #424242;
}

.links_body {
    padding: 10px;
    text-align: left;
    font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
    font-weight: bold;
    // color: #ffffff;
    background-color: #d0f0c0;
    // background-image: url(/images/tumblr_inline_mnd920iJ7C1qz4rgp540.gif);
    line-height: 1.3em;
    display: none;
    border-left: 1px solid #424242;
    border-right: 1px solid #424242;
    border-bottom: 1px solid #424242;
    border-radius: 0px 0px 5px 5px;
}

.links_body a {
    color: white;
}

