/* Scrollbar styling */
::-webkit-scrollbar {
    width: 10px;
    /* Vertical Width */
    height: 10px;
    /* Horizontal Height */
}

/* Scrollbar Track Color */
::-webkit-scrollbar-track {
    background: transparent;
    border-radius: 10px;
}

/* Scrollbar Thumb Color */
::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 0px;
    border: 2px solid transparent;
}

/* Thumb Hover */
::-webkit-scrollbar-thumb:hover {
    background: #1a4b3e;
}

#statuscafe {
    padding: .1em;
    background-color: transparent;
    border: none;
    font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
}

#statuscafe-username {
    margin-bottom: .5em;
    font-size: 12px;
}

// #statuscafe-content {
//     margin: 0 1em 0.1em 1em;
// }

.change ul{
    font-size: small;
}

ul li::before {
  content: "★ ";
}

.content-text {
    padding: 10px;
    width: 99%;
    height: 99%;
    border: 1px dotted rgb(212, 188, 142);
}

.caption {
    padding: 0;
    display: block;
    width: 24%;
    margin: auto;
    font-size: 11px;
    background-color: #d0f0c0;
}

@media (max-width: 800px) {
    /* 主容器：移除 Content 区域 */
    .container {
        grid-template-areas:
            "Header"
            "Sidebar"
            "Footer";
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        width: 100%;
        padding: 0;
        margin: 0;
        overflow-x: hidden;
    }

    /* 隐藏 Content 区块 */
    .Content {
        display: none !important;
    }

    /* 其他区块全宽显示 */
    .Header,
    .Sidebar,
    .Footer {
        width: 100vw;
        max-width: 100%;
        margin: 0;
        padding: 15px;
        box-sizing: border-box;
        border-radius: 0;
        border-style: dashed !important;
    }

    /* Header 样式 */
    .Header {
        font-size: 60px;
        text-align: center;
        color: #1a4b3e;
    }

    .Header img {
        width: 80px;
        height: auto;
    }

    /* Sidebar 样式 */
    .Sidebar {
        background-color: #e8f5e8;
        border-top: 2px dashed #1a4b3e;
        border-bottom: 2px dashed #1a4b3e;
        align-items: stretch;
        padding: 15px 10px;
    }

    .links_list {
        width: 70%;
        margin: 0 auto;
    }

    .links_head {
        font-size: 1.2em;
        padding: 5px;
    }

    .links_body a {
        display: block;
        // margin: 8px 0;
        padding: 5px;
        text-align: center;
        border-bottom: 1px dotted #1a4b3e;
        color: white;
    }

    // .links_body a:hover {
    //     background-color: #1a4b3e;
    //     color: white;
    //     text-decoration: line-through;
    // }

    /* Footer */
    .Footer {
        background-color: #d0f0c0;
        border: 2px dashed #1a4b3e;
        font-size: 14px;
        text-align: center;
        padding: 10px;
    }

    /* 聊天框类组件 */
    .chat {
        width: 90%;
        margin: 10px auto;
        font-size: 14px;
    }

    .border-text {
        font-size: 14px;
        top: -12px;
    }

    /* 图片自适应 */
    .Sidebar img {
        max-width: 100%;
        height: auto;
    }

    /* 列表 */
    li {
        font-size: 14px;
    }
}