/* ========================================= */
/* 对话框 */
.message {
  font-size: 16px;
  width: 80%;
  margin: 1rem 0;
  text-align: right;
}

.message.--self .message__text {
  background-color: var(--card-background);
}

.dark .message.--self .message__text {
  background-color: var(--card-background);
  border: 1px solid #b7dddd15;
  }

.message.--self .message__text {
  text-align: left;
}

.message.--self .message__meta {
  text-align: right;
}


.message:not(.--self) {
  text-align: left;
}

.message__inner {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  align-items: flex-end;
}

.message__inner img {
  order: 1;
}

.message:not(.--self) .message__inner {
  justify-content: flex-start;
}

.message:not(.--self) .message__inner img {
  order: 0;
}

.message__text {
  margin-left: 8px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-right: 8px;
  --list-opacity: 1;
  background-color: var(--card-background);
  border: 3px solid #ccc;
  padding-left: 6px; 
  padding-right: 6px; 
  border-radius: 8px;
}

.message__meta {
  color: #888;
  font-size: 0.8em;
}

.message.--self {
  margin-left: auto;
}

.message:not(.--self) {
  margin-right: auto;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
}

.grid-item {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  position: relative;
}

.grid-item .message__img {
  object-fit: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 8px;
}

.fullsize {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}

img.avatar{
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: inset 0 0 0 1px rgba(105, 105, 105, 0.1);
  flex-shrink: 0;
  margin:unset !important;
}

img.message__img.medium-zoom-image.medium-zoom-image--opened {
  object-fit: contain;
  position: absolute;
  width: 100%;
  height: 100%;
  border: unset;
  box-shadow: unset;
}

img.message__img {
  margin: unset !important;
}

/* 定义手机端样式 */
@media screen and (max-width: 768px) {
  .message {
      width: 90%;
  }
}

/* ========================================= */
/* 文字颜色渐变 */
.colorfulfont {
    background: linear-gradient(to right, rgb(25, 221, 238), #ed4588); 
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

/* ========================================= */
/* 文字黑幕 */
.shady {
    color:#000;
    font-weight: bold;
    box-shadow: 0px -20px 0px rgba(0,0,0,1) inset; 
    transition: all 0.3s ease;
}
.shady:hover{
   font-weight: bold;
    color:#FFF;
    box-shadow: 0px -20px 0px rgba(0,0,0,1) inset; 
}


/* ========================================= */
/* 文字包裹图片和图片脚注 */
/* 图片环绕容器 - 默认：移动端样式 */
.wrap,
.wrap-left {
  float: none;
  width: 100%;
  max-width: 320px;
  margin: 1rem auto;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  /* 移动端：居中，不浮动 */
}

/* 图片样式 */
.wrap img,
.wrap-left img {
  display: block;
  width: 100%;
  height: auto;
  border: none;
}

/* 图片描述 */
.image-desc {
  font-size: 0.85rem;
  color: #666;
  margin-top: 0.5rem;
  text-align: center; /* 移除!important，除非绝对必要 */
  font-style: italic;
  line-height: 1.3;
  display: flex; /* 使用flex布局 */
  justify-content: center; /* 水平居中 */
}

/* 桌面端：开启浮动环绕 */
@media (min-width: 768px) {
  .wrap {
    float: right;
    margin: 0 0 1rem 1.5rem;
  }

  .wrap-left {
    float: left;
    margin: 0 1.5rem 1rem 0;
  }

  /* 可选：桌面端稍大一点 */
  .wrap,
  .wrap-left {
    width: 360px;
  }

  .wrap + p,
  .wrap-left + p {
  clear: both;
  margin-top: 1rem;
  }
}

/* 更大屏幕优化 */
@media (min-width: 1024px) {
  .wrap,
  .wrap-left {
    width: 400px;
  }
}

/* ========================================= */
/* 通用box */
.custom-block-box {
  padding: 16px;
  margin: 16px 0;
  background-color: rgba(215, 224, 216, 0.534);
  border-radius: 8px; 
  border-top: 2px solid #2d6f49;
  border-bottom: 5px solid #2d6f49;
  border-left: 5px solid #2d6f49;
  border-right: 2px solid #2d6f49;
}

.custom-block-box ol, .custom-block-box ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 20px;
  list-style-type: disc;
}


/* ========================================= */
/* fancy-box */
.fancy-box-container {
  display: flex;
  justify-content: center;  
  // align-items: center;      
  width: 100%;
  margin: 10px 0;
}

.fancy-box {
  background: #7aa469;
  padding: 1px;
}

.fancy-box-inner {
  background: #dff7d5;
  padding: 3px;
}

.fancy-box-content {
  border: 1px dashed #000;
  padding: 12px;
  font-size: 14px;
  line-height: 1.5;
  box-sizing: border-box;
}


/* ========================================= */
/* fancy-quote */
blockquote.fancy-quote {
  position: relative;
  max-width: 620px;
  margin: 20px auto;
  text-align: center;
  border: none;
  padding: 0;
  background: transparent;
}

blockquote.fancy-quote p {
  // font-family: 'Abril Fatface', cursive;
  // font-size: 2.8rem;
  color: #e74848;
  border: 2px solid #4d4c4c;
  border-radius: 20px;
  padding: 25px;
  margin: 0;
  position: relative;
}

/* 装饰线 */
blockquote.fancy-quote p::after {
  content: "";
  position: absolute;
  bottom: -62px;
  left: 50px;
  border: 2px solid #e74848;
  border-radius: 0 100% 0 0;
  width: 60px;
  height: 60px;
  border-bottom: none;
  border-left: none;
  z-index: 3;
}

blockquote.fancy-quote p::before {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 50px;
  width: 80px;
  border: 6px solid #292a2b;
  z-index: 2;
}

blockquote.fancy-quote footer {
  // color: #ffffff;
  // font-size: 1.3rem;
  margin: 0;
  padding-top: 15px;
  margin-left: 110px;
  padding-left: 12px;
  text-align: left;
}