/* ========================================= */
/* 上下篇导航样式 */
.pagination {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
  padding: 1rem;
  font-size: 0.95rem;
  color: #555;
}

.pagination a {
  color: #3c5832;
  text-decoration: none;
  transition: color 0.3s ease;
}

.pagination a:hover {
  color: #2a3f24;
  text-decoration: underline;
}

.pagination .disabled {
  color: #aaa;
  cursor: not-allowed;
  font-style: italic;
}



@media (max-width: 800px) {
  .sidebar-links {
  font-size: 10px;
  margin-top: 10px;
  }

.footer-note {
  font-size: 10px;
}
