// -------------- THEME SWITCHER -------------- //
@mixin theme($--bg-color, $--primary-text-color, $--secondary-text-color, $--link-color, $--visited-link-color, $--highlight) {
  // background-color: $--bg-color;
  background-color: rgba($--bg-color,0.6);
  background-image: url("/images/tumblr_inline_mgzuk2HsQx1qid2nw.png");
  background-blend-mode: overlay;

  color: $--primary-text-color;

  a {
    color: inherit;;
    &:hover {color: $--link-color;}
    // &:visited { color: $--visited-link-color; }
  }

  details {
    border: thin solid $--primary-text-color;
  }

  details summary {
    color: $--primary-text-color;
  }

  details[open] summary {
    border-bottom: 1px solid $--primary-text-color;
  }

  // pre {
  //   background: $--bg-color;
  // }

  // code:not(pre > code) {
  //   background-color: $--primary-text-color;
  //   color: $--bg-color;
  // }

  *:target {
    background: $--highlight;
    color: $--primary-text-color;
   }

  table, th, td {
    border: thin solid $--primary-text-color;
    // background-color:#FFFFFF;
    // filter: Alpha(opacity=50); 
    // width: "300";
    // height: "100";
  }

  .toc {
    border: thin solid $--primary-text-color;
    padding: 1rem;
  }

  figcaption { color: $--secondary-text-color; }

  // blockquote {
  //   border: thin solid $--primary-text-color;
  // }
}

@mixin dark-appearance {
  @include theme(#000000, #ffffff, #000000, #79a8ff, #f78fe7, #2f3849); // modus-vivendi
}

@mixin light-appearance {
  @include theme(#ffffff, #000000, #595959, #01513a, #8f0075, #dae5ec); // modus-operandi
}

body[a="dark"] { @include dark-appearance; }
body[a="light"] { @include light-appearance; }

@media (prefers-color-scheme: dark) {
  body[a="auto"] { @include dark-appearance; }
}

@media (prefers-color-scheme: light) {
  body[a="auto"] { @include light-appearance; }
}

// -------------------------------------------- //

html { height: 100%; }

body {
  font-family: "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
  font-size: 17px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
  position: relative;
}

h1 {
  line-height: 1.65;
  font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
}

h2, h3, h4, h5, h6 { 
  margin-top: 1.1rem; 
  font-family: "Huiwen-mincho", "LXGW WenKai", "Noto Sans SC", "Source Han Sans CN", "sans-serif";
}

p { margin: 1rem 0; }

li { margin: 0.4rem 0; }

a {
  text-decoration: underline;
  &:hover {
  position: relative;
  top: 1px;
  left: 1px;
  }
}

hr {
  text-align: center;
  border: 0;
  margin: 2rem 0;

  &:before { content: '/////' }
  &:after { content: attr(data-content) '/////' }
}

table { width: 100%; }

table, th, td {
  border-collapse: collapse;
  padding: 0.4rem;
}

// blockquote {
//   padding: 1rem;

//   p { margin: 0; }
// }

blockquote {
  width:90%;
  margin:10px auto;
  color: #555555;
  padding:0.52em 10px 0.52em 10px;
  border-left:8px solid #78C0A8 ;
  position: relative;

  p { margin: 0; }
}

blockquote::after{
  content: '';
}

blockquote span{
  display:block;
  color:#333333;
  font-style: normal;
  font-weight: bold;
  margin-top:1em;
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

figcaption {
  text-align: center;
  opacity: 0.5;
}

details {
  padding: 1rem;
}

details summary {
  text-decoration: none;
}

details[open] summary {
  margin-bottom: 0.5em;
  padding-bottom: 0.5em;
}

.post-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.w {
  max-width: 640px;
  margin: 0 auto;
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
}

.w2 {
  max-width: 840px;
  margin: 0 auto;
}

.other-w {
  max-width: 640px;
  margin: 0 auto;
  // padding: 4rem 2rem;
  display: flex;
  flex-direction: column;
}

.toc {
  padding: 1rem;
}

.xian {
  border-bottom: 3px dotted #3c3133;
}

.enddiver {
  margin-top: 36px;
  margin-bottom: 36px;
}

.main-content {
  margin-top: 26px;
}

mark {
  background-color: #eef6ab;
}